.section {
  margin-top: 150px;
  margin-bottom: 27px;

  &__inner {
    margin-bottom: 50px;
    display: grid;
    grid-template-columns: 1fr 650px;
    align-items: start;
  }

  &__img-wrap {
    margin-top: 23px;
    margin-left: -13px;

    &--season {
      height: 520px;
      margin-top: 34px;
      margin-right: 0;
      margin-left: -310px;
    }

    &--lol {
      margin: 0;
    }
  }

  &__img {
    display: block;
    max-width: 100%;
    height: auto;
  }

  &__content {
    text-align: right;
  }

  &__title {
    margin: 0 0 25px 0;

    font-size: 72px;
    font-weight: 700;
    color: $black;
  }

  &__subtitle {
    margin: 0 0 53px 0;
    padding-right: 10px;

    font-size: 28px;
    font-weight: 500;
    color: $black;
  }

  &__text {
    margin: 0 0 80px 0;
    padding-right: 10px;

    font-size: 22px;
    color: $black;
  }

  &__links-btn__ingame {
    margin-right: 22px;
    display: inline-block;
    vertical-align: middle;
    padding: 18px 76px;
    font-family: 'Museo Sans';
    font-weight: 500;
    font-size: 22px;
    line-height: 1.1;
    text-align: center;
    color: $black;
    background: $light-yellow;
    border: 0;
    border-radius: 12px;
    text-decoration: none;
    cursor: pointer;

    &--season {
      background: $orange;
      color: $white;
    }

    &--lol {
      background: $yellow;
      color: $black;
    }
  }

  &__links-btn__details {
    padding: 10px;
    display: inline-flex;
    align-items: center;
    vertical-align: middle;
    text-align: center;
    text-decoration: none;
    border: 0;
    background: $white;
    cursor: pointer;
  }

  &__links-text {
    padding-bottom: 5px;
    margin-left: 19px;
    font-family: 'Museo Sans';
    font-weight: 500;
    font-size: 22px;
    line-height: 1.1;
    color: $black;
    border-bottom: 3px dotted $light-yellow;

    &--season {
      border-color: $orange;
    }

    &--lol {
      border-color: $yellow;
    }
  }

  &__links-icon {
    color: $light-yellow;

    &--season {
      color: $orange;
    }

    &--lol {
      color: $yellow;
    }
  }

  &__columns {
    margin: 0 0 50px 0;
    padding: 0 0 0 62px;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    column-gap: 44px;
    align-items: start;
    list-style: none;
  }

  &__columns-text {
    margin: 0;
    font-size: 22px;

    &--first {
      margin-bottom: 27px;
    }
  }

  &__info {
    padding-left: 15px;
    display: flex;
    align-items: center;

    &-icon {
      margin-right: 20px;
      color: $light-violet;
    }

    p {
      margin: 0;
      max-width: 565px;
      font-size: 18px;
      color: $light-violet;
    }
  }
}

/* Media */
@media (max-width: 1100px) {
  .section {
    margin: 50px 0 30px 0;

    &__inner {
      margin: 0 0 40px 0;
      display: flex;
      flex-direction: column;
      align-items: center;
    }

    &__img-wrap {
      margin: 0;

        &--season {
          margin: 34px 0 0 -310px;
          height: 300px;
      }
    }

    &__content {
      text-align: center;
    }

    &__title {
      margin: 0 0 15px 0;
      font-size: 60px;
    }

    &__subtitle {
      margin: 0 0 30px 0;
      padding: 0;
      font-size: 23px;
    }

    &__text {
      margin: 0 0 40px 0;
      padding: 0;
      font-size: 18px;
    }

    &__links-btn__ingame {
      margin: 0 15px 0 0;
      padding: 15px 45px;
      font-size: 18px;
    }

    &__links-btn__details {
      padding: 8px;
    }

    &__links-text {
      padding-bottom: 3px;
      margin-left: 10px;
      font-size: 18px;
      border-bottom-width: 2px;
    }

    &__links-icon {
      width: 30px;
      height: 30px;
    }

    &__columns {
      margin: 0 0 40px 0;
      padding: 0;
      display: grid;
      grid-template-columns: 1fr 1fr 1fr;
      column-gap: 44px;
      align-items: start;
      list-style: none;
    }

    &__info {
      padding: 0;
    }
  }
}

@media (max-width: 768px) {
  .section {
    &__title {
      margin: 0 0 15px 0;
      font-size: 45px;
      line-height: 1;
    }

    &__subtitle {
      margin: 0 0 25px 0;
      font-size: 20px;
    }

    &__text {
      margin: 0 0 30px 0;
      font-size: 17px;
    }

    &__links-btn__ingame {
      padding: 15px 40px;
      font-size: 17px;
    }

    &__links-text {
      margin-left: 10px;
      font-size: 17px;
    }

    &__columns {
      margin: 0 0 30px 0;
      padding: 0;
      display: flex;
      flex-wrap: wrap;
    }

    &__columns-text {
      margin: 0 0 20px 0;
      font-size: 18px;

      &--first {
        margin: 0;
      }
    }

    &__info {
      &-icon {
        width: 25px;
        height: 25px;
        margin: 10px 10px 0;
        flex-shrink: 0;
      }

      p {
        font-size: 17px;
      }
    }
  }
}

@media (max-width: 375px) {
  .section {
    &__links-btn__ingame {
      margin: 0 auto 15px;
      display: block;
    }

    &__links-btn__details {
      margin: 0 auto;
    }
  }
}