@import "colors";
@import "tools";
@import "fonts";
@import "buttons";
@import "content";
@import "city.scss";

html, body {
  margin: 0;
  padding: 0;
  background: $white;
  color: $gray-x1a;
  font-family: 'Museo Sans', sans-serif;
  font-size: 10px;
  font-weight: 500;
  line-height: 1.2;
  min-height: 100vh;
  @media (min-width: 540px) {
    font-size: 16px;
  }
}

.container {
  width: 100%;
  max-width: 1170px;
  margin: 0 auto;
  padding: 0 16px;
  position: relative;
}

.container--special {
  width: 100%;
  max-width: 1230px;
  margin: 0 auto;
  padding: 0 15px;
}

header {
  position: relative;
  z-index: 10;
  display: grid;
  grid-gap: 32px;
  grid-template-columns: 32px 1fr 32px;
  grid-template-areas: 'nav logo lk' 'city city city';
  @media (min-width: 540px) {
    grid-template-columns: 1fr 80px 4fr;
    grid-template-areas: 'nav logo city';
    .lk {
      display: none;
    }
  }
  @media (min-width: 940px) {
    grid-template-columns: 1fr 32px 100px 1fr 32px;
    grid-template-areas: 'nav x logo city lk';
    .lk {
      display: block;
    }
  }

  nav {
    grid-area: nav;
    align-self: center;

    ul {
      margin: 0;
      padding: 0;
      display: flex;
      list-style: none;
      align-items: center;

      li {
        margin-right: 1.4em;
        font-size: 1.4em;

        &:nth-child(2),
        &:nth-child(3) {
          display: none;
          @media (min-width: 1200px) {
            display: block;
          }
        }

        a, button {
          color: $white;
          text-decoration: none;
          display: inline-block;

          svg {
            width: 2em;
          }
        }

        a {
          padding-bottom: .2em;
        }
      }
    }
  }

  .logo {
    color: $yellow;
    grid-area: logo;

    &--city {
      color: $light-yellow;
    }

    &--season {
      color: $orange;
    }

    svg {
      width: 100%;
    }
  }

  .lk {
    color: $white;
    grid-area: lk;

    svg {
      width: 2.7em;
    }
  }

}

.select-city {
  grid-area: city;
  display: flex;
  align-self: center;
  justify-content: flex-end;
  cursor: pointer;

  .city {
    font-size: 1.4em;
    color: $white;
    padding-top: .6em;

    .phone {
      display: block;
      text-align: right;
      color: $light-violet;
      font-size: .75em;
      text-decoration: none;

      &--city {
        color: $light-yellow;
      }

      &--season {
        color: $orange;
      }

      &--lol {
        color: $yellow;
      }
    }
  }

  .btn {
    color: $yellow;

    &--city {
      color: $light-yellow;
    }

    &--season {
      color: $orange;
    }
  }

  button {
    margin-left: 1em;

    svg {
      width: 2.7em;
    }
  }
}

.page {
  min-height: 100vh;
  width: 100%;
  overflow: hidden;
}

.box {
  width: 100%;
}

.slider {
  width: 100%;
  height: 100%;
  overflow: hidden;
  display: grid;

  .slider-controls {
    grid-area: controls;
    display: flex;
    color: $main-purple;
    margin: 0 0 2.2em 0;

    h3 {
      border: 2px solid $main-purple;
      border-radius: 0.6em;
      margin: 0 0.8em;
      padding: 0.6em 2.9em;
    }
  }

  .slider-additional {
    grid-area: additional;
    padding: 0 0 1.5em 0;
  }

  .slider-info {
    grid-area: info;
    padding: 0;
    font-size: 1.38em;
  }

  .slider-overflow {
    grid-area: overflow;
    overflow: hidden;
    display: flex;
    /*
    тут задавать количество слайдов умноженное на 100% через JS
    width: 200%;
    */

    .slide {
      overflow: hidden;
      width: 100%;
    }
  }
}

.cards-section {
  margin: -5em -16px 0;
  position: relative;
  top: 6em;
  z-index: 12;

  .overflow-container {
    //.beautiful-scrollbar;
    overflow-x: auto;
    padding: 2em;
    @media (min-width: 1100px) {
      padding: 1em 0;
      overflow: initial;
    }
  }

  .show-all {
    font-size: 1.4rem;

    .btn {
      .text {
        color: $main-purple;
      }
    }
  }
}

.link-cards {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  min-width: 1000px;
  grid-gap: 24px;
  list-style: none;
  margin: 0;
  padding: 0;

  li {
    box-shadow: 0 4px 18px 0 rgba($main-purple, 0.3);
    overflow: hidden;
    border-radius: 1.5em;

    &.card-primary a {
      background-color: $yellow;

      .heading {
        color: $dark-yellow;
      }
    }

    &:not(.card-primary) {
      box-shadow: 0 0 20px 0 fade($main-purple, 50%);
    }
  }

  a {
    display: block;
    background-color: $white;
    color: $gray-x1a;
    padding: 2em 2em;
    text-decoration: none;


    > * {
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
      display: block;
    }

    .heading {
      font-size: 1.3em;
      margin: 0 0 1.2em 0;
      color: $main-purple;
    }

    .title {
      font-size: 1.9em;
      font-weight: 700;
      margin-bottom: 0.4em;
    }

    .address {
      font-size: 1.4em;
    }

    .date {
      font-size: 1.4em;
    }
  }

  + .text-center .btn {
    font-size: 1.3em;
    margin-top: 1.7em;
  }
}

.logo-box {
  display: inline-flex;
  justify-content: space-between;
  align-items: center;
  margin: 3em 0 1em;

  .icon {
    width: auto;

    &:nth-child(1) {
      height: 5.4em;
    }

    &:nth-child(2) {
      height: 2em;
      margin: 0 2em;
    }

    &:nth-child(3) {
      height: 4.4em;
    }
  }
}

.slider-box {
  .slide {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    @media (min-width: 900px) {
      flex-direction: row;
    }

    .slide-content {
      padding: 24px;
    }
  }

}

.tns-nav {
  display: flex;
  align-items: center;
  justify-content: center;

  button {
    width: 12px;
    height: 12px;
    border: none;
    border-radius: 50%;
    overflow: hidden;
    margin: 2px;
    display: block;
    font-size: 1px;
    padding: 0;
    background: $main-purple;

    &:hover, &.tns-nav-active {
      background: $yellow;
    }
  }
}

footer {
  font-weight: 300;
  font-size: 1.1em;
  @media (max-width: 480px) {
    .btn {
      min-width: auto;
    }
  }

  .row {
    color: $white;
    background-color: $gray-x26;
    padding: 10px 0;

    &:first-child {
      .container {
        @media (min-width: 900px) and (max-width: 1200px) {
          justify-content: center;
        }
      }
    }

    .container {
      display: flex;
      justify-content: space-between;
      align-items: center;
    }

    .left-box {
      text-align: center;

      @media (min-width: 900px) {
        text-align: left;
      }
    }

    .right-box {
      text-align: center;

      @media (min-width: 900px) {
        text-align: right;
      }

      > * {
        vertical-align: middle;
      }
    }

    .logo-box {
      font-size: 0.5em;
      color: $yellow !important;
      margin: 0;
      display: none;
      @media (min-width: 900px) {
        display: flex
      }
    }

    .email {
      display: inline-block;
      margin-right: 2em;
      padding: 1em;
    }

    p {
      margin: 0;
    }

    a {
      color: $white;
      text-decoration: none;

      &:hover {
        color: $yellow;
      }
    }

    .mobile-logo {
      display: block;
      color: $yellow !important;
      @media (min-width: 900px) {
        display: none;
      }

      .icon {
        width: 50px;
        height: 50px;
      }
    }

    nav {
      display: none;
      @media (min-width: 1200px) {
        display: block;
      }

      ul {
        margin: 0;
        padding: 0;
        list-style: none;
        display: flex;
        align-items: center;

        li {
          display: inline-block;
          margin-right: 2em;

          a {

          }

          .btn {
            svg {
              height: 1.8em;
            }
          }
        }
      }
    }

    + .row {
      padding: 15px 0;
      color: $gray-x66;
      background-color: $gray-x1a;

      a {
        color: inherit;

        &:hover {
          color: $yellow;
        }
      }

      .left-box {
        @media (min-width: 900px) {
          max-width: 33%;
        }
      }

      .right-box {
        width: 100%;
        border-bottom: 1px solid $gray-x26;
        padding-bottom: 1em;
        margin-bottom: 1em;
        @media (min-width: 900px) {
          border: none;
          padding: 0;
          margin: 0;
          width: 33%;
        }
      }

      .container {
        flex-direction: column-reverse;

        @media (min-width: 900px) {
          flex-direction: row;
        }
      }
    }
  }
}
