.cards {
  margin: 0 -16px 0;
  position: relative;
  top: 70px;
  z-index: 12;

  &--season {
    margin: -57px -16px 0;
  }

  &--lol {
    margin: -90px -16px 0;
  }
}

.cards .overflow-container {
  //.beautiful-scrollbar;
  overflow-x: auto;
  padding: 0 0 0 20px;

  @media (min-width: 1100px) {
    overflow: initial;
    padding: 0;
  }
}

.cards__list {
  margin: 0;
  padding: 0 0 30px 0;
  list-style: none;

  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-column-gap: 20px;
  min-width: 1120px;

  &-item {
    background-color: $white;
    padding: 37px;
    border-radius: 24px;
    overflow: hidden;
    box-shadow: 0 4px 18px 0 rgba($main-purple, 0.3);

    a {
      display: block;
      text-decoration: none;
    }
  }

  &-title {
    color: $main-purple;
    font-size: 18px;
    margin: 0 0 25px 0;
  }

  p {
    margin: 0;
    color: $gray-x1a;
    font-size: 30px;
    font-weight: 700;
  }

  &-text {
    &--third {
      max-width: 150px;
    }

    &--third-wider {
      max-width: 275px;
    }

    sup {
      font-size: 18px;
      color: $main-purple;
    }
  }
}

/* Media */
@media (max-width: 540px) {
  .cards {
    &--lol {
      margin: -70px -16px 0;
    }
  }

  .cards__list {
    &-item {
      padding: 18px;
    }

    &-title {
      font-size: 15px;
      margin: 0 0 15px 0;
    }

    p {
      font-size: 20px;
    }

    &-text {
      &--first {
        max-width: 200px;
      }

      &--second {
        max-width: 175px;
      }

      span {
        font-size: 15px;
      }
    }
  }
}