// output: false
@import "colors";

* {
  box-sizing: border-box;
}

[hidden] {
  display: none;
}

.visually-hidden {
  position: absolute !important;
  clip: rect(1px 1px 1px 1px); /* IE6, IE7 */
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0 !important;
  border: 0 !important;
  height: 1px !important;
  width: 1px !important;
  overflow: hidden;
}

.text-center {
  text-align: center;
}

.text-right {
  text-align: right;
}

.text-left {
  text-align: left;
}

.reverse {
  transform: scale(-1, 1);
}

.reverse-vertical {
  transform: scale(1, -1);
}

.beautiful-scrollbar {
  scrollbar-width: thin;
  scrollbar-height: thin;
  scrollbar-color: $main-purple $gray-x1a;

  &::-webkit-scrollbar-track {
    background-color: transparent;
  }

  &::-webkit-scrollbar {
    width: 4px;
    height: 4px;
  }

  &::-webkit-scrollbar-thumb {
    border-radius: 10px;
    background-color: transparent;
  }
}

.box-center {
  position: relative;
  height: 4vw;
  max-height: 40px;

  > * {
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
  }
}

cite {
  &::before {
    content: open-quote;
  }

  &::after {
    content: close-quote;
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
