.slider-mentor{
  margin-bottom: 71px;

  .container--special{
    position: relative;
    padding: 85px 15px 120px;
  }

  &--secondary {
    margin-bottom: 11px;
  }

  &__inner {
    margin-left: 1px;
  }

  &__item {
    position: relative;
    overflow: hidden;
  }

  &__bulb {
    background-image: url("../img/bulb.png");
    background-repeat: no-repeat;
    background-size: contain;
    width: 424px;
    height: 542px;
    position: absolute;
    bottom: -11px;
    left: -80px;
    z-index: -1;
  }

  &__cup {
    background-image: url("../img/cup.png");
    background-repeat: no-repeat;
    background-size: contain;
    width: 674px;
    height: 653px;
    position: absolute;
    bottom: 120px;
    right: 15px;
  }

  &__dice {
    background-image: url("../img/dice.png");
    background-repeat: no-repeat;
    background-size: contain;
    width: 149px;
    height: 171px; 
    position: absolute;
    top: 38px;
    right: 38%;
  }

  &__man {
    background-image: url("../img/slider-mentor-dimitry.png");
    background-repeat: no-repeat;
    background-size: contain;
    width: 1200px;
    height: 660px;
    position: absolute;
    bottom: 120px;
    right: 15px;
  }

  &__chess {
    background-image: url("../img/chess-horse.png");
    background-repeat: no-repeat;
    background-size: contain;
    width: 383px;
    height: 470px;
    position: absolute;
    top: 274px;
    right: -192px;
  }

  &__content {
    background: linear-gradient(#824cc7, #2b0d83);
    border-radius: 40px;
    padding: 72px 0 77px 102px;

    &--secondary {
      padding: 73px 0 102px 100px;
    }
  }

  &__content-inner {
    position: relative;
    z-index: 3;
  }

  &__suptitle {
    margin: 0 0 9px 0;
    font-size: 28px;
    letter-spacing: -0.3px;
    color: $white;
    font-weight: 500;
  }

  &__title {
    margin: 0 0 18px 0;
    font-size: 72px;
    letter-spacing: -1.5px;
    font-weight: 700;
    color: $white;
  }

  &__subtitle {
    margin: 0 0 80px 0;
    max-width: 375px;
    font-size: 22px;
    color: $light-pink;
    font-weight: 500;

    span {
      display: inline-block;
      margin-top: 26px;
    }

    &--special {
      max-width: 415px;
    }
  }

  &__text {
    margin: 0 0 34px 0;
    max-width: 345px;
    font-size: 22px;
    line-height: 1.1;
    font-style: italic;
    color: $white;
  }

  &__btn {
    padding: 0;
    vertical-align: middle;
    text-align: center;
    text-decoration: none;
    border: 0;
    cursor: pointer;

    display: inline-flex;
    align-items: center;
    background: transparent;

    &-icon {
      margin-left: 4px;
      width: 32px;
      height: 32px;
      color: $yellow;
    }

    &-text {
      margin-left: 13px;
      color: $yellow;
      font-size: 22px;
      font-weight: 500;
      font-family: 'Museo Sans';
    }
  }

  &__btn-ingame {
    margin-right: 22px;
    display: inline-block;
    vertical-align: middle;
    padding: 18px 76px;
    font-family: 'Museo Sans';
    font-weight: 500;
    font-size: 22px;
    line-height: 1.1;
    text-align: center;
    color: $black;
    background: $yellow;
    border: 0;
    border-radius: 12px;
    text-decoration: none;
    cursor: pointer;
  }

  &__btn-details {
    padding: 10px;
    display: inline-flex;
    align-items: center;
    vertical-align: middle;
    text-align: center;
    text-decoration: none;
    border: 0;
    background: transparent;
    cursor: pointer;
  }

  &__icon {
    color: $light-yellow;
  }

  &__text-additional {
    padding-bottom: 5px;
    margin-left: 19px;
    font-family: 'Museo Sans';
    font-weight: 500;
    font-size: 22px;
    line-height: 1.1;
    color: $white;
    border-bottom: 3px dotted $white;
  }

  .tns-nav {
    margin-top: -74px;
    position: relative;
    z-index: 2;

    button {
      outline: none;
      margin: 4px;
      background-color: #d0c2e4;

      &:hover, &.tns-nav-active {
        background: #6534a7;
      }
    }
  }
}

/* Media */
@media (max-width: 1230px) {
  .slider-mentor {
    &__man {
      background-size: 80%;
      bottom: -12px;
      right: -225px;
    }

    &__cup {
      background-size: 80%;
      bottom: -11px;
      right: -120px;
    }
  }
}

@media (max-width: 1100px) {
  .slider-mentor {
    &__cup {
      background-size: 65%;
      bottom: -109px;
      right: -221px;
    }

    &__btn-ingame {
      padding: 15px 45px;
      font-size: 18px;
    }

    &__text-additional {
      font-size: 18px;
      border-bottom: 2px dotted #fff;
    }

    &__icon {
      width: 30px;
      height: 30px;
    }
  }
}

@media (max-width: 1058px) {
  .slider-mentor {
    &__suptitle {
      font-size: 25px;
    }

    &__title {
      font-size: 60px;
    }

    &__subtitle {
      font-size: 18px;
    }

    &__text {
      font-size: 18px;
    }

    &__man {
      background-size: 65%;
      bottom: -111px;
      right: -405px;
    }

    &__bulb {
      background-size: 60%;
      bottom: -181px;
      left: -32px;
    }

    &__dice {
      background-size: 65%;
      top: 50px;
      right: 33%;
    }

    &__chess {
      background-size: 50%;
      top: 422px;
      right: -273px;
    }
  }
}

@media (max-width: 988px) {
  .slider-mentor {
    &__cup {
      background-size: 50%;
      bottom: -206px;
      right: -322px;
    }
  }
}

@media (max-width: 922px) {
  .slider-mentor {
    &__man {
      background-size: 50%;
      bottom: -210px;
      right: -585px;
    }

    &__content--secondary {
      padding: 73px 0 102px 65px;
    }
  }
}

@media (max-width: 857px) {
  .slider-mentor {
    &__cup {
      background-size: 40%;
      bottom: -272px;
      right: -389px;
    }
  }
}

@media (max-width: 768px) {
  .slider-mentor {
    &--secondary {
      margin: 0;
    }

    &__bulb {
      display: none;
    }

    &__man {
      background-size: 35%;
      bottom: -309px;
      right: -766px;
    }

    &__cup {
      background-size: 35%;
      bottom: -304px;
      right: -423px;
    }

    &__dice {
      display: none;
    }

    &__chess {
      display: none;
    }

    &__btn-ingame {
      padding: 15px 40px;
      font-size: 17px;
    }

    &__text-additional {
      font-size: 17px;
    }
  }
}

@media (max-width: 700px) {
  .slider-mentor {
    &__cup {
      background-size: 29%;
      bottom: -344px;
      right: -464px;
    }
  }
}

@media (max-width: 658px) {
  .slider-mentor {
    .container--special {
      padding: 50px 15px 95px;
    }

    &__content {
      padding: 68px 5px 66px;
      text-align: center;
    }

    &__suptitle {
      font-size: 22px;
    }

    &__title {
      font-size: 45px;
    }

    &__subtitle {
      font-size: 17px;
      margin: 0 auto 60px;
    }

    &__btn-text {
      font-size: 18px;
    }

    &__text {
      font-size: 17px;
      margin: 0 auto 34px;
    }

    &__man {
      display: none;
    }

    &__cup {
      display: none;
    }
  }
}

@media (max-width: 540px) {
  .slider-mentor{
    &__btn-ingame {
      display: block;
      margin: 0 auto 10px;
    }
  }
}