// output: false
@import "colors";

.btn {
  background: none;
  border: 2px solid;
  text-decoration: none;
  cursor: pointer;
  color: inherit;
  padding: 0.8em;
  font-size: inherit;
  font-family: inherit;
  font-weight: 500;
  border-radius: .5em;
  vertical-align: bottom;
  text-align: center;
  min-width: 260px;

  &.btn-icon {
    padding: 0;
    border-radius: 50%;
    min-width: 0;
    border: none;

    .icon, svg {
      color: inherit;
    }
  }

  &.btn-transparent {
    background-color: transparent !important;
    font-weight: normal;
  }

  &.btn-info {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    padding: 0.5em;
    background-color: transparent !important;
    border: none;

    > * {
      margin: 0 0.35em;
      display: inline-block;

      &:first-child {
        margin-left: 0;
      }

      &:last-child {
        margin-right: 0;
      }
    }

    .icon, svg {
      width: 1.8em;
      color: inherit;
    }

    .text {
      color: inherit;
      border-bottom: .15em dotted white;
    }

    &.btn-accent {
      color: $main-purple !important;

      .text {
        color: $black;
        border-bottom-color: $main-purple !important;
      }
    }

    &.btn-primary {
      color: $yellow !important;

      .text {
        border-bottom: none;
      }
    }
  }

  &.btn-primary {
    background-color: $yellow;
    border-color: $yellow;
    color: $gray-x1a;

    &.btn-transparent {
      color: $yellow;
    }
  }

  &.btn-accent {
    background-color: $main-purple;
    border-color: $main-purple;
    color: $white;

    &.btn-transparent {
      color: $main-purple;
    }
  }


}
