// output: false
@import "colors";

.hero {
  position: relative;
  background: url(../img/main-bgr.png) top;
  background-size: cover;
  padding-top: 12px;

  &--city {
    background-image: url('../img/city-battle__header-bg.jpg');
    background-position: top;
    background-repeat: no-repeat;
  }

  &--season {
    background-image: url('../img/season__header-bg.jpg');
    background-position: top;
    background-repeat: no-repeat;
  }

  &--lol {
    background-image: url('../img/lol__header-bg.jpg');
    background-position: top;
    background-repeat: no-repeat;
  }

  @media (min-width: 940px) {
    padding-top: 45px;
  }

  color: $white;

  @media (min-width: 540px) and (max-width: 940px) {
    background-position-x: calc(50% - 25vw);
  }

  &.open-popup {
    .select-city-box {
      display: block;
    }
  }

  .select-city-box {
    display: none;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 100vh;
    overflow-y: auto;
    z-index: 13;
    background: rgba(0, 0, 0, .8);
    backdrop-filter: blur(5px);
    padding-top: 82px;
    animation: fadeIn 350ms ease-in-out forwards;

    .heading {
      max-width: 400px;
      margin: 0 auto;

      > * {
        margin-bottom: 1em;
      }

      @media (min-width: 700px) {
        max-width: 100%;
        display: grid;
        grid-gap: 32px;
        align-items: center;
        grid-template-areas: 'search country city';
        grid-template-columns: 1fr 1fr 17em;
      }

      @media (min-width: 960px) {
        grid-template-areas: 'search country city x';
        grid-template-columns: 1fr 1fr 1fr 32px;
      }

      .search-city {
        grid-area: search;

        label {
          display: block;
          border: 2px solid $yellow;
          border-radius: 10px;
          position: relative;

          input {
            width: 100%;
            background: transparent;
            border: none;
            padding: 0.7em 1.2em;
            color: $white;
            font-family: inherit;
            font-size: 1.35rem;
          }

          .icon {
            width: 1.2em;
            position: absolute;
            right: 1.4em;
            top: 50%;
            transform: translateY(-50%);
            color: $yellow;
          }
        }
      }

      .select-country {
        grid-area: country;

        label {
          display: block;
          background: $yellow;
          text-align: center;
          border-radius: 10px;
          position: relative;
        }

        select {
          display: block;
          width: 100%;
          background: transparent;
          border: none;
          font-family: inherit;
          font-size: 1.4em;
          padding: 0.7em 1em;
          font-weight: 500;
          -webkit-appearance: none;
          -moz-appearance: none;
          appearance: none;
        }

        .icon {
          position: absolute;
          width: 1.2em;
          top: 50%;
          right: 1.4em;
          transform: translateY(-50%);
          color: $black;
        }
      }

      .select-city {
        justify-content: center;

        .select-city-box-btn-after-icon {
          display: none;
        }

        @media (min-width: 700px) {
          justify-content: flex-end;

          .select-city-box-btn-after-icon {
            display: block;
            position: absolute;
            width: 2em;
            height: 2em;
            top: 9rem;
            right: 50%;
            transform: translateX(50%);
            font-size: 0.4em;
            color: $light-violet;

            &:hover {
              color: $white;
            }
          }

          .btn {
            position: relative;
          }
        }


      }
    }

    .cities-list {
      column-count: 2;
      display: inline-block;
      font-size: 1.4em;
      margin: 3.4em 0;
      list-style: none;
      font-weight: 500;
      padding: 0 10px;
      @media (min-width: 960px) {
        column-width: 278px;
        height: 43vh;
        min-height: 300px;
        column-count: unset;
      }

      li {
        margin-bottom: 0.7em;
      }

      a {
        color: $white;
        text-decoration: none;

        &:hover {
          text-decoration: underline;
        }
      }
    }
  }

  .content-container {
    padding: 16px 32px;
    @media (min-width: 940px) {
      padding: 28px 0 0 72px;
    }

    .btn {
      position: relative;
      z-index: 10;
      font-size: 0.72em;

      &:first-child {
        margin-right: 0.6em;
      }
    }
  }

  h1 {
    color: inherit;
    font-size: 3.7em;
    padding: 0;
    margin: 0 0 .2em 0;
    @media (min-width: 940px) {
      font-size: 5em;
    }

    + p {
      text-align: center;
      @media (min-width: 940px) {
        text-align: left;
      }
    }
  }

  p {
    font-size: 1.6em;
    margin: 0 0 2.5em 0;
    @media (min-width: 940px) {
      font-size: 1.9em;
    }
  }

  .btn-info {
    color: $white;
    border-bottom-color: $white;
  }

  .sub-info {
    margin: 2em 0 7.5em;
    font-size: 1.1em;
    max-width: 250px;
    @media (min-width: 700px) {
      margin-bottom: 2.5em;
      max-width: 370px;
    }

    &:after {
      content: '';
      display: block;
      position: absolute;
      background: url("../img/dmitriy-main.png") bottom right no-repeat;
      background-size: contain;
      bottom: 0;
      right: -22%;
      width: 130%;
      height: 100%;
      @media (min-width: 700px) {
        width: 100%;
      }
    }

    h6 {
      font-size: 1.2em;
      margin: 0 0 0.5em 0;
      font-weight: 500;
    }

    p {
      font-size: 1em;
      color: $light-violet;
    }

  }
}

.lets-play {
  background: $white;
  padding: 180px 0 117px;

  .lets-play-heading {
    margin-bottom: 45px;
  }

  .container {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  img {
    display: block;
    max-width: 100%;
  }

  .content-container {
    width: 100%;

    p {
      font-size: 1.4em;
      margin: 0 0 1em 0;
    }

    .slider {
      display: grid;
      grid-template-areas: 'controls' 'overflow' 'additional' 'info';

      @media (min-width: 940px) {
        grid-template-columns: 5fr 7fr;
        grid-template-rows: auto 1fr auto;
        grid-template-areas: 'additional controls' 'additional overflow' 'additional info';
      }

      .slider-controls, .slider-info {
        justify-content: center;
        text-align: center;
        @media (min-width: 940px) {
          justify-content: flex-start;
          text-align: left;
        }
      }

      .slider-additional {
        padding-bottom: 1.5em;

        @media (min-width: 940px) {
          padding-bottom: 0;
        }
      }

    }
  }
}

.fun-day {
  background: url("../img/laugh-day-bgr.jpg") top no-repeat;
  background-size: cover;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;

  .subtitle {
    text-align: center;
    text-transform: uppercase;
    color: $white;
    letter-spacing: 0.4em;
    margin: 0;
    font-size: 1.2em;
    padding: 3.3em 0 0;
    font-weight: 900;
  }

  .fun-day-heading {
    display: block;
    margin: 0 auto -5%;
    max-width: 70%;
  }

  .rounded-text {
    display: block;
    max-width: 100%;
    min-width: 560px;
    width: 1000px;
    color: $white;
    font-weight: 600;
    margin: 0 auto;
  }
}

.corp {
  padding-top: 145px;

  .content-container {
    position: relative;
    z-index: 1;
    @media (min-width: 700px) {
      padding-top: 8em;
    }

    h2 {
      font-size: 4em;
      color: $main-purple;
      margin: 0 0 0.27em 0;
      text-shadow: 0 0 5px $white;
      text-align: center;
      @media (min-width: 700px) {
        font-size: 5em;
        text-align: left;
      }
    }

    > p {
      margin: 0 0 2em 0;
      font-size: 1.6em;
      text-align: center;

      @media (min-width: 700px) {
        text-align: left;
        font-size: 1.9em;
        margin-bottom: 3em;
        max-width: 550px;
      }
    }

    .buttons {
      font-size: 1.3rem;
      max-width: 270px;
      @media (min-width: 800px) {
        max-width: 100%;
      }

      .btn {
        margin-bottom: 10px;
      }
    }
  }

  .sub-info {
    color: $main-purple;
    margin: 4em 0 0 0;
    padding-bottom: 17em;
    @media (min-width: 700px) {
      margin-top: -2em;
      padding-bottom: 4em;
    }

    &::before {
      content: '';
      display: block;
      position: absolute;
      background: url(../img/krivenya.jpg) top right no-repeat;
      background-size: 100%;
      bottom: 0;
      right: -41%;
      width: 122%;
      height: 30em;
      z-index: -1;
      @media (min-width: 450px) {
        height: 35em;
      }
      @media (min-width: 700px) {
        background-size: contain;
        right: -25%;
        width: 100%;
        height: 30em;
      }
      @media (min-width: 950px) {
        right: -31%;
        width: 100%;
        height: 44em;
      }
      @media (min-width: 1100px) {
        right: -19%;
      }
      @media (min-width: 1300px) {
        right: -4%;
      }
    }

    h6 {
      font-size: 1.3em;
      margin: 0 0 0.5em 0;
    }

    p {
      font-size: 1.1em;
      max-width: 50%;
    }
  }


}

.info-box {
  border-top: 2px solid $main-purple;
  color: $main-purple;

  .info-box-content {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 3em 0;

    .icon {
      margin-right: 1em;
      width: 1.6em;
      flex-shrink: 0;
    }

    p {
      font-size: 1.15em;
      margin: 0;
    }

    a {
      color: inherit;

      &:hover {
        text-decoration: none;
      }
    }
  }
}

.city-battle {
  background: url("../img/city-battle-logo-bgr.jpg") right no-repeat;
  background-size: cover;
  color: $white;
  @media (min-width: 700px) {
    background-position: top;
  }

  .content-container {
    display: grid;
    grid-template-areas: 'heading' 'logo' 'description' 'buttons';
    @media (min-width: 700px) {
      grid-template-areas: 'logo heading' 'logo description' 'logo buttons';
      grid-template-columns: 1fr 1fr;
    }

    .heading {
      grid-area: heading;
      padding: 2em 0 0 0;
      text-align: center;

      @media (min-width: 700px) {
        padding: 5.5em 0 2.2em 0;
        text-align: left;
      }

      p {
        margin: 0;
        text-transform: uppercase;
        letter-spacing: 0.6em;
        font-weight: bold;
        color: $light-yellow;
      }

      h2 {
        margin: 0.1em 0 0 0;
        font-size: 5em;
      }
    }

    .logo {
      grid-area: logo;
      display: flex;
      align-items: center;
      justify-content: center;

      img {
        max-width: 100%;
        margin: 0 0 2em 0;
        @media (min-width: 700px) {
          margin: 1em 0 0 0;
        }
      }
    }

    .description {
      grid-area: description;
      font-size: 1.4em;
      font-weight: bold;

      p {
        margin: 0 0 1.1em 0;
      }
    }

    .buttons {
      grid-area: buttons;
      padding: 2em 0 5em 0;
      font-size: 1.3em;
      text-align: center;
      @media (min-width: 700px) {
        padding: 4em 0 3em 0;
      }

      .btn {
        margin-bottom: 1em;
      }
    }
  }
}

.franchise {
  padding-top: 7em;

  .content-container {
    background: url("../img/dmitry-white-filled.jpg") bottom left no-repeat;
    background-size: 64%;
    display: flex;
    align-items: flex-end;
    flex-direction: column;

    h2 {
      margin: 0;
      color: $main-purple;
      font-size: 3em;
      padding: 0 0 0.3em 0;
      @media (min-width: 800px) {
        font-size: 4.8em;
      }
    }

    p {
      margin: 0;
      font-size: 1.3em;
      @media (min-width: 800px) {
        font-size: 1.8em;
      }
    }

    .buttons {
      font-size: 1.3em;
      padding-top: 4.1em;
      text-align: center;
    }

    .sub-info {
      margin-top: 3em;
      padding-bottom: 4em;
      color: $main-purple;

      h6 {
        margin: 0 0 0.6em 0;
        font-size: 1.3em;
      }

      p {
        font-size: 1em;
        max-width: 19em;
      }
    }
  }

  @media (max-width: 1000px) {
    .content-container {
      align-items: center;

      .sub-info {
        margin-top: 4em;
        align-self: flex-end;
        padding-bottom: 15em;
      }
    }
  }
}

.school {
  background: url("../img/school-quiz-bgr.jpg");
  background-size: cover;
  text-align: center;
  color: $white;
  padding-bottom: 3.7em;

  .content-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    position: relative;

    &:before, &:after {
      content: '';
      background-size: contain;
      position: absolute;
      background-repeat: no-repeat;
      background-position: center;
      display: none;
      @media (min-width: 800px) {
        display: block;
      }
    }

    &:before {
      background-image: url("../img/decor-pencil.png");
      top: -125px;
      right: -13px;
      width: 300px;
      height: 266px;
    }

    &:after {
      background-image: url("../img/decor-arrow-cursor.png");
      bottom: -179px;
      right: 64px;
      width: 172px;
      height: 209px;
    }

    .heading {
      display: block;
      max-width: 100%;
    }

    p {
      font-size: 1.3em;
      margin: 2em 0 2.4em;
      max-width: 850px;
    }

    .btn {
      font-size: 1.4em;
    }
  }

}

.reviews {
  padding: 5em 0;

  .btn {
    min-width: auto;
    font-size: 1.1em;
    padding: 0;
  }

  h3 {
    font-size: 1.8em;
    margin: 0 0 0.3em 0;
    font-weight: normal;
  }

  p {
    font-size: 1.2em;
    max-width: 17em;
    margin: 0 0 0.5em 0;
  }

  cite {
    font-size: 1.4em;
    max-width: 14em;
    display: inline-block;
    text-align: left;
  }

  .slide-content {
    @media (min-width: 900px) {
      text-align: right;
    }
  }
}
