.rating {
  margin-bottom: 81px;

  &__inner {
    text-align: center;

    display: flex;
    align-items: center;
    flex-direction: column;
  }

  &__title {
    margin: 0 0 6px 0;

    font-size: 72px;
    font-weight: 700;
    color: $black;

    span {
      white-space: nowrap;
    }
  }

  &__icon-drop {
    margin-left: 10px;
    background: transparent;
    color: $main-purple;
    border: 0;
    cursor: pointer;
  }

  &__title-wrap {
    display: flex;
    align-items: center;
  }

  &__subtitle {
    margin: 0 0 60px 0;
    max-width: 715px;

    font-size: 22px;
    color: $black;

    &--secondary {
      margin: 0 0 40px;
    }
  }

  &__direction {
    padding: 0;
    margin: 0 0 26px 0;
    list-style: none;

    display: grid;
    grid-template-columns: repeat(6, 160px);
    column-gap: 8px;

    &-item {
      display: grid;
    }

    &-btn {
      cursor: pointer;
      padding: 14px 0;
      border: 2px solid $second-purple;
      border-radius: 10px;
      background: $white;

      font-family: 'Museo Sans', sans-serif;
      font-size: 22px;
      color: $second-purple;
      white-space: nowrap;

      &--active {
        background: $second-purple;
        color: $white;
      }
    }
  }

  &__text {
    margin: 0 0 40px 0;
    max-width: 810px;
    color: $second-purple;
    font-size: 18px;
  }

  &__table-overflow {
    width: 100%;
    padding: 0 85px 0 33px;
  }

  &__table {
    margin-bottom: 69px;
    width: 100%;
    font-family: 'Museo Sans', sans-serif;
    border-collapse: separate;
    border-spacing: 10px;
    color: $second-purple;

    position: relative;

    th {
      white-space: nowrap;
      font-size: 18px;
      color: #646464;
      font-weight: 500;
    }

    td {
      background-color: transparent;
      border: 1px solid #d1d0e7;
      border-radius: 10px;
      font-size: 22px;

      height: 60px;
    }

    tr &-number {
      border: 0;
      height: 30px;
    }

    tr &-team {
      text-align: left;
      width: 762px;
      padding: 0 20px;

      &--first {
        background-color: $second-purple;
        color: $white;
      }

      &--top {
        background-color: #efeaff;
      }
    }

    tr &-games {
      width: 60px;
    }

    tr &-points {
      background: #efeaff;
      width: 160px;
    }
  }


  &__additional {
    margin-left: 8px;
    display: flex;
    flex-direction: column;
    align-items: center;

    &-btn {
      margin: 0 0 27px 0;
      display: block;
      vertical-align: middle;
      padding: 18px 61px;
      font-family: 'Museo Sans', sans-serif;
      font-size: 22px;
      line-height: 1.1;
      color: $white;
      font-weight: 500;
      text-align: center;
      background: $second-purple;
      border: 0;
      border-radius: 10px;
      text-decoration: none;
      cursor: pointer;
    }

    &-text {
      margin: 0;
      font-size: 18px;
      max-width: 315px;
      color: $second-purple;
    }
  }

  &__error {
    margin-top: 46px;
    margin-left: 11px;
    margin-bottom: 260px;
    display: flex;
    flex-direction: column;
    align-items: center;

    &-img {
      position: relative;
      left: 27px;
      top: -2px;
    }

    &-text {
      margin: 0 0 45px 0;
      max-width: 615px;
      font-size: 22px;
      color: $black;
    }

    &-btn {
      margin: 0;
      display: block;
      vertical-align: middle;
      padding: 18px 63px;
      font-family: 'Museo Sans', sans-serif;
      font-size: 22px;
      line-height: 1.1;
      color: $white;
      font-weight: 500;
      text-align: center;
      background: $second-purple;
      border: 0;
      border-radius: 10px;
      text-decoration: none;
      cursor: pointer;
    }
  }
}

/* Media */
@media (max-width: 1100px) {
  .rating {
    &__additional-btn {
      padding: 15px 40px;
      font-size: 18px;
    }

    &__error {
      margin-bottom: 200px;
    }

    &__error-btn {
      padding: 15px 40px;
      font-size: 18px;
    }

    &__error-text {
      font-size: 18px;
    }
  }
}

@media (max-width: 1024px) {
  .rating {
    &__table-overflow{
      padding: 0;
    }

    &__title {
      font-size: 60px;
    }

    &__subtitle {
      margin: 0 0 40px 0;
      font-size: 19px;
    }

    &__direction {
      grid-template-columns: repeat(3,160px);
      column-gap: 25px;
      row-gap: 15px;
    }

    &__additional {
      margin-left: 0;
    }

    &__error {
      margin-bottom: 150px;
    }
  }
}

@media (max-width: 768px) {
  .rating {
    margin-bottom: 50px;

    &__title {
      margin: 0 0 15px 0;
      font-size: 45px;
    }

    &__subtitle {
      margin: 0 0 40px 0;
      font-size: 18px;
    }

    &__direction {
      grid-template-columns: repeat(2,160px);

      &-btn {
        padding: 10px 0;
        font-size: 18px;
      }
    }

    &__text {
      font-size: 17px;
    }

    &__table {
      padding-bottom: 40px;
      margin: 0;

      td {
        font-size: 18px;
      }
    }

    &__additional-btn {
      font-size: 17px;
    }

    &__additional-text {
      font-size: 17px;
    }

    &__error {
      margin-bottom: 100px;
    }

    &__error-btn {
      font-size: 17px;
    }

    &__error-text {
      font-size: 17px;
    }
  }
}

@media (max-width: 545px) {
  .rating {
    &__title {
      font-size: 40px;
    }

    &__subtitle {
      font-size: 17px;
    }

    &__title-wrap {
      flex-direction: column;
    }

    &__icon-drop {
      margin: 0 0 15px 0;
    }

    &__table {
      td {
        font-size: 17px;
      }
    }
  }
}

@media (max-width: 450px) {
  .rating {
    &__table-overflow {
      margin: 0 -15px 20px;
      overflow-x: auto;
      max-width: calc(100% + 30px);
    }

    &__table {
      min-width: 500px;
    }
  }
}

@media (max-width: 405px) {
  .rating {
    &__title {
      span {
        font-size: 30px;
      }
    }
  }
}

@media (max-width: 375px) {
  .rating {
    &__direction {
      grid-template-columns: repeat(2,125px);
    }
  }
}
